.loader-body{
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border-radius: 32px;
  justify-content: center;
  flex-direction: column;
    background: #fff;
    z-index: 9;
    opacity: 0.9;
}
.loader-circle-2 {
	position: relative;
	width: 70px;
	height: 70px;
	display: inline-block;
  z-index: 9;
}
.loader-circle-2:before,
.loader-circle-2:after {
	content: "";
	display: block;
	position: absolute;
	border-width: 5px;
	border-style: solid;
	border-radius: 50%;
}
.loader-circle-2:before {
	width: 70px;
	height: 70px;
	border-bottom-color: #009688;
  border-style: solid;
  border-radius: 50%;
  background: linear-gradient(white, white) padding-box,
  linear-gradient(91.48deg, #8A63F4 0.29%, #B090FF 100%) border-box;
  border: 2px solid transparent;
	border-top-color: transparent;
	border-left-color: transparent;
  border-top: none;
  border-left: none;
	animation: loader-circle-2-animation-2 1s linear infinite;
}
.loader-circle-2:after {
	width: 40px;
	height: 40px;
  border-style: solid;
  border-radius: 50%;
  background: linear-gradient(white, white) padding-box,
  linear-gradient(91.48deg, #8A63F4 0.29%, #B090FF 100%) border-box;
  border: 2px solid transparent;
	border-top-color: transparent;
	border-left-color: transparent;
  border-top: none;
  border-left: none;
	top: 22%;
	left: 22%;
	animation: loader-circle-2-animation 0.85s linear  infinite;
}
@keyframes loader-circle-2-animation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(-360deg);
	}
}
@keyframes loader-circle-2-animation-2 {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}







.quote {
  position: relative;
  display: block;
  overflow: hidden;
  border: 1px solid #8A63F4;
}


@keyframes criss-cross-left {
  0% {
    left: -10px;
  }
  50% {
    left: 35%;
    width: 50px;
    height: 50px;
  }
  70%{
        left: 20%;
  }100% {
    background: linear-gradient(91.48deg, #8A63F4 0.29%, #B090FF 100%);
    left: 110%;
    width: 375px;
    height: 375px;
 

  }
}
@keyframes criss-cross-left2 {
  0% {
    left: -10px;
  }
  50% {
    left: 35%;
    width: 50px;
    height: 50px;
  }
  70%{
        left: 20%;
  }100% {
    background: linear-gradient(91.48deg, #fff 0.29%, #fff 100%);
    left: 110%;
    width: 375px;
    height: 375px;
 

  }
}

@keyframes criss-cross-right {
  0% {
    right: -20px;
  }
  30% {
    right: 30%;
    width: 20px;
    height: 20px;
  }
  40%{
     right: 20%;
  }
  100% {
    right: 80%;
    width: 125px;
    height: 125px;
  }
}
.quote {
  position: relative;
  color: #8556b0;
}

.quote:before, .quote:after {
  position: absolute;
  top: 50%;
  content: '';
  width: 20px;
  height: 20px;
  background-color: #7038a2;
  border-radius: 50%;
}


.quote:before {
  left: -20px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  
}

.quote:after {
  background: linear-gradient(91.48deg, #8A63F4 0.29%, #B090FF 100%);
  right: -20px;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}

.quote:hover {
    color: #fff;
}

.quote:hover:before {

  -webkit-animation: criss-cross-left 1.2s both;
          animation: criss-cross-left 1.2s both;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  
}

.quote:hover:after {
  
  -webkit-animation: criss-cross-right 1.2s both;
          animation: criss-cross-right 1.2s both;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.quote .gredient-text{
  position: relative;
  z-index: 8;
}

